.app__frontend {
  display: flex;
  height: 700px;
  align-items: center;
  flex-direction: column;
  padding-bottom: 2rem;
  background-color: rgb(226, 228, 253);
}
.app__frontend-title {
  display: flex;
  justify-content: center;
  color: rgb(134, 117, 38);
}
.app__frontend-title h1 {
  font-family: Georgia, "Times New Roman", Times, serif;
  margin: 2rem;
  font-size: 45px;
  font-weight: 300;
}
.app__frontend-content {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 1350px) {
  .app__frontend {
    height: auto;
  }
  .app__frontend-title h1 {
    margin-top: 4rem;
    font-size: 35px;
  }
  .app__frontend-content {
    flex-direction: column;
  }
}
